* {
  margin: 0px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

form {
  margin: 0 auto;
  max-width: 600px;
  padding: 0px 20px;
}

.button-save-delete {
  margin-left: auto;
  padding: 10px;
  font-size: 18px;
  border-radius: 5px;
  font-weight: bold;
  border: none;
}

.button-save-delete:hover {
  cursor: pointer;
  box-shadow: 4px 4px 4px darkgrey;
}

.button-save-delete_delete {
  background-color: red;
  color: white;
}

.button-save-delete_save {
  border: 2px solid darkgray;
}

.button-save-delete_save .modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
}

.modal {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(80, 80, 80, 0.6);
  display: flex;
  justify-content: center;
}

.modal__button-container {
  display: flex;
  justify-content: space-evenly;
}

.modal__button {
  height: 50px;
  padding: 10px;
  background-color: white;
  border-radius: 5px;
  border: none;
  border-top: 2px solid whitesmoke;
  border-left: 2px solid whitesmoke;
  box-shadow: 3px 3px 1px lightgrey;
  font-size: 21px;
  font-weight: bold;
}

.modal__button:hover {
  cursor: pointer;
  box-shadow: none;
}

.modal__button_confirm {
  background-color: red;
  color: white;
}

.modal__button_cancel:hover {
  background-color: whitesmoke;
}

.modal__content {
  background-color: #fefefe;
  margin-top: 75px;
  width: 325px;
  height: 250px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.modal__close {
}

.modal__close-button {
  font-size: 42px;
  background-color: white;
  color: red;
  border: none;
}

.modal__close-button:hover,
.modal__close-button:focus {
  cursor: pointer;
}

.modal__text {
  display: flex;
  justify-content: center;
  font-size: 21px;
}

.navbar {
  background-color: black;
  color: white;
  margin-bottom: 20px;
  padding: 0px;
  top: 0px;
  height: 50px;
}

.navbar__content {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
}

.navbar__link {
  text-decoration: none;
  color: white;
  border: 2px solid white;
  font-size: 21px;
  padding: 5px;
}

.recipe__button-section {
  display: flex;
  justify-content: space-between;
  padding: 0px 10px;
}

.recipe-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  margin-bottom: 20px;
}

.recipe-container__legend {
  font-size: 18px;
  font-weight: bold;
}

.recipe-ingredient-name-container {
  margin-bottom: 5px;
}

.recipe__input_heading {
  font-size: 16px;
  border: 1px solid lightgray;
}

.recipe-ingredient__input_name {
  width: 100%;
  height: 30px;
  font-size: 16px;
  border: 1px solid lightgray;
}

.recipe-ingredient__input {
}

.recipe-ingredient-percent-wt-btn-container {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  grid-gap: 5px;
}

.recipe__input-percent-container {
  border: 1px solid lightgray;
  display: flex;
  align-items: center;
  height: 30px;
}

.recipe-ingredient__input_percent {
  width: 100%;
  border: none;
  padding-left: 15%;
  font-size: 16px;
}

.recipe__calculated-weight {
  border: 1px solid black;
}

.recipe-banner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  color: white;
  padding: 0px 20px;
}

.recipe-banner__text {
  font-weight: bold;
  font-size: 18px;
}

.recipe__button_delete {
  font-weight: bold;
  font-size: 28px;
  height: 30px;
  line-height: 0px;
  width: 100%;
  background-color: #ffb699;
  color: white;
  border: none;
  border-radius: 5px;
  text-align: center;
}

.recipe__button_delete:hover {
  cursor: pointer;
  background-color: #ff8859;
}

.recipe-button_add {
  height: 30px;
  width: 100px;
  margin-top: 5px;
  font-weight: bold;
  background-color: white;
  color: black;
  cursor: pointer;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.recipe-header {
  position: sticky;
  top: 0px;
  background-color: white;
  padding: 5px;
}

.recipe-section {
  max-width: 575px;
  margin: 20px auto 0px;
  padding: 0px 10px;
}

.recipe-section_ingredients {
  margin-top: 50px;
}

.recipe__input_percent-text {
  margin-right: 10px;
}

.recipe-ingredient-container {
  border: 1px solid lightgray;
  box-shadow: 5px 5px 5px lightgrey;
  padding: 10px 5px;
  margin: 10px 0px;
  display: block;
}

.recipe-ingredient-container__calc-weight-div {
  border: 1px solid lightgray;
  line-height: 30px;
  display: flex;
  justify-content: space-evenly;
  height: 30px;
}

.recipe-ingredient-container__calc-weight-value {
  margin: 0px;
}

.recipe-list {
  list-style: none;
  padding: 0px 20px;
}

.recipe-list__link {
  text-decoration: none;
  color: black;
}

.recipe-list__list-item {
  border: 1px solid lightgray;
  padding: 10px;
  max-width: 500px;
  margin: 10px auto;
  border-radius: 10px;
  box-shadow: 2px 2px 2px darkgrey;
}

.recipe-list__list-item:hover {
  box-shadow: 4px 4px 4px darkgray;
  cursor: pointer;
}

.save-button {
  margin-left: auto;
}

@media only screen and (min-width: 600px) {
  .recipe-container__legend {
    font-size: 21px;
  }

  .recipe-ingredient-container {
    grid-template-columns: 2fr 3fr;
    display: grid;
    grid-gap: 5px;
  }
}
